.account-sidebar-scrollbar::-webkit-scrollbar {
    height: 124px;
    width: 0.25rem
}
  .account-sidebar-scrollbar::-webkit-scrollbar-track {
    --tw-bg-opacity: 1;
    background-color: rgba(164, 168, 175, var(--tw-bg-opacity))
}
  .account-sidebar-scrollbar::-webkit-scrollbar-thumb {
    --tw-bg-opacity: 1;
    background-color: rgba(55, 65, 81, var(--tw-bg-opacity))
}

